<script>
export default {
  computed:{
    empresa(){
      //let objEmpresa = JSON.stringify({id:1, nome: 'Itiban'})
      //localStorage.setItem("empresa", objEmpresa)
      
      let empresaSelecionada = {}
      empresaSelecionada.nome = 'Infinity Group S.A.'
      
      if((localStorage.getItem("empresa") !== 'undefined') && (localStorage.getItem("empresa") !== null)){
